#language-selector {
    z-index: 10002;
    position: fixed;
    top: 0;
    right: 0;
    text-align: right;
    color: _palette(accent4);

    li {
        display: inline-block;
        margin: 0;
        margin-right: 1em;
        padding: 0 1em 0 0;

        &:last-child {
            padding-right: 0;
        }

        .icon {
            color: inherit;

            &:before {
                font-size: 1.25em;
            }
        }
    }

    a {
        border: none;
    }
    
    .en:lang(en), .fr:lang(fr), .de:lang(de){
        font-weight: bold;
    }
}
