/* Page Wrapper */

#page-wrapper {
    @include vendor('display', 'flex');
    @include vendor('align-items', 'center');
    @include vendor('justify-content', 'flex-start');
    @include vendor('flex-grow', '1');
    @include vendor('flex-shrink', '1');
    height: 100%;
    padding: 5rem;

    @include orientation(portrait) {
        padding-left: 2rem;
        padding-right: 2rem;
    }
}
